<template>
  <div class="index2" :class="rootClass">
    <div class="bg">
      <div class="top"></div>
      <div class="left"></div>
      <div class="right"></div>
      <div class="bottom"></div>
    </div>
    <div class="colorBar">
      <div
        class="color"
        v-for="(item, index) in colors"
        :key="index"
        :style="{ background: item }"
        @click="changeColor(item)"
      ></div>
    </div>
    <div class="main">
      <div class="left">
        <div class="head">
          <h1>left-head</h1>
        </div>
        <div class="content">
          <my-content title="left-content" />
        </div>
      </div>
      <div class="right">
        <div class="head">
          <h1>right-head</h1>
        </div>
        <div class="content">
          <my-content title="right-content" />
        </div>
      </div>
    </div>
  </div>
</template>
<script lang="ts">
import { computed, defineComponent, ref } from "vue";
import content from "./content.vue";
export default defineComponent({
  components: {
    myContent: content,
  },
  setup() {
    const rootClass = ref("red");
    const colors = ref(["#f56565", "#48bb78", "#9f7aea"]);
    function changeColor(color: any) {
      switch (color) {
        case "#f56565":
          rootClass.value = "red";
          break;
        case "#48bb78":
          rootClass.value = "green";
          break;
        case "#9f7aea":
          rootClass.value = "purple";
          break;
      }
      console.log(rootClass.value);
    }

    return { colors, changeColor, rootClass };
  },
});
</script>
<style lang="scss" scoped>
//多色主题
.red {
  --color-1: #feb2b2;
  --color-2: #fc8181;
  --color-3: #e53e3e;
  --color-4: #c53030;
}
.green {
  --color-1: #9ae6b4;
  --color-2: #68d391;
  --color-3: #38a169;
  --color-4: #2f855a;
}
.purple {
  --color-1: #d6bcfa;
  --color-2: #b794f4;
  --color-3: #805ad5;
  --color-4: #6b46c1;
}
.index2 {
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem;
  //背景
  .bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--color-2);
    //背景里的白色装饰
    .top {
      position: absolute;
      top: 0;
      left: 0;
      height: 8rem;
      width: 100%;
      padding-right: 10rem;
      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background: var(--color-1);

        border-radius: 0 0 8rem 0;
      }
    }
    .bottom {
      position: absolute;
      bottom: 0;
      right: 0;
      height: 8rem;
      width: 100%;
      padding-left: 10rem;
      &::before {
        content: "";
        display: block;
        height: 100%;
        width: 100%;
        background: var(--color-1);

        border-radius: 8rem 0 0 0;
      }
    }
    .left {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      height: 12rem;
      width: 6rem;
      background: var(--color-1);

      border-radius: 0 10rem 10rem 0;
    }
    .right {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate(-1.7rem, -50%);
      height: 12rem;
      width: 6rem;
      background: var(--color-1);

      border-radius: 10rem 0 0 10rem;
    }
  }
  //选色
  .colorBar {
    padding: 0.4rem;
    position: fixed;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 1.5rem;
    height: 12rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: #fff;
    border-radius: 0.4rem 0 0 0.4rem;
    .color {
      width: 1rem;
      height: 1rem;
      border-radius: 0.5rem;
      cursor: pointer;
    }
  }
  .main {
    flex-grow: 1;
    min-height: 20rem;
    height: 100%;
    width: 60rem;
    background: #fff;
    z-index: 999;
    border-radius: 0.5rem;
    overflow: hidden;
    display: flex;
    h1 {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .left {
      width: 50%;
      min-width: 600px;
      display: flex;
      flex-direction: column;
      background: #fff;
      overflow: hidden;
      .head {
        min-height: 16rem;
        box-shadow: 0 4px 6px -1px rgb(0 0 0 / 10%),
          0 2px 4px -1px rgb(0 0 0 / 6%);
      }
      .content {
        overflow: auto;
      }
    }
    .right {
      width: 50%;
      min-width: 20rem;
      display: flex;
      flex-direction: column;
      overflow: hidden;
      .head {
        background: var(--color-4);
        min-height: 8rem;
      }
      .content {
        background: var(--color-3);
        overflow: auto;
      }
    }
  }
  @media screen and (max-width: 940px) {
    .main {
      flex-direction: column;
      overflow: auto;
    }
    .left {
      overflow: auto;
      width: 100% !important;
    }
    .right {
      overflow: auto !important;

      width: 100% !important;
    }
    .content {
      overflow: auto !important;

      overflow: hidden !important;
    }
  }
}
</style>
